.btn--increment {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  position: relative;
  border: none;
  color: white;
  z-index: 1;
  display: grid;
  grid-template-rows: 25% 75%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; }
  .btn--increment:after {
    pointer-events: none;
    content: " ";
    position: absolute;
    top: 0;
    bottom: 0;
    opacity: 0;
    width: 200%;
    z-index: 49;
    -webkit-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out; }
  .btn--increment.scored {
    z-index: 50 !important; }
    .btn--increment.scored:after {
      opacity: 1; }
  .btn--increment:disabled {
    background: #527980; }
    .btn--increment:disabled .score {
      -webkit-transform: scale(0.8);
              transform: scale(0.8); }
    .btn--increment:disabled .team-name {
      -webkit-transform: scale(1.1);
              transform: scale(1.1); }
  .btn--increment:active {
    background: #527980; }
    .btn--increment:active .score {
      -webkit-transform: scale(0.8);
              transform: scale(0.8); }
    .btn--increment:active .team-name {
      -webkit-transform: scale(1.1);
              transform: scale(1.1); }
  .btn--increment:first-of-type {
    -webkit-animation: flyInLeft 0.7s forwards;
            animation: flyInLeft 0.7s forwards; }
  .btn--increment:last-of-type {
    -webkit-animation: flyInRight 0.7s forwards;
            animation: flyInRight 0.7s forwards; }
  .btn--increment p {
    margin: 0;
    font-family: "Archivo Black", sans-serif;
    font-size: 3rem; }
  .btn--increment .team-name {
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
    padding-top: 32px;
    text-align: center; }
  .btn--increment .score {
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
    height: 75%;
    font-size: 14rem; }

